import { RepostoriEntity } from '../../core/domain/entity/reposiory.entity';
import SolutionsMapper from './solucions-mapper';
import { Solution } from '../../core/interfaces/Solution';

class SolutionRepository extends RepostoriEntity {
  enpoint = `${process.env.PUBLIC_URL}/data/solutions.json`;
  findAll() {
    return fetch(this.enpoint)
      .then(res => res.json())
      .then((data: Solution[]) => {
        return SolutionsMapper.mapFrom(data);
      });
  }
}

export default new SolutionRepository();
