import React from 'react';

export interface IDescriptionArtifact {
  responsive: boolean;
  tecnology: string;
  description: string;
  benefits: string[];
  dark?: boolean;
  rotateCard?: () => void;
}

export const DescriptionArtifact: React.FC<IDescriptionArtifact> = props => {
  if (props.responsive) {
    return (
      <div className='card front'>
        <div className='card-body'>
          <h2 className='h2'>{props.tecnology}</h2>
          <h6 className='card-text mb-4'>{props.description}</h6>
          <button
            className='btn btn-primary btn-lg btn-block'
            onClick={props.rotateCard}
          >
            Capacidades
          </button>
        </div>
      </div>
    );
  }

  return (
    <div
      className={
        'col-xl-5 col-lg-4 col-md-12 col-sm-12' +
        (props.dark ? ' text-white' : '')
      }
    >
      <h2 className={props.dark ? 'h2 text-white' : ''}>{props.tecnology}</h2>
      <h6 className={props.dark ? 'text-white' : ''}>{props.description}</h6>
      <h4 className={props.dark ? 'text-white' : ''}>Beneficios</h4>
      <ol>
        {props.benefits.map((benefit, index) => {
          return <li key={`benefit-${index}`}>{benefit}</li>;
        })}
      </ol>
    </div>
  );
};
