import React from 'react';
import logo from '../../../../../assets/img/logo.svg';
export default () => {
  return (
    <div className='mobile-nav-header d-flex align-items-center justify-content-between w-100'>
      <button
        className='navbar-toggler navbar-toggler-left x collapsed'
        type='button'
        data-toggle='collapse'
        data-target='#navbarText'
        aria-controls='navbarText'
        aria-expanded='false'
        aria-label='Toggle navigation'
      >
        <span className='navbar-toggler-icon'></span>
        <span className='navbar-toggler-icon'></span>
        <span className='navbar-toggler-icon'></span>
      </button>
      <a className='navbar-brand' href='javscript:void(0)'>
        <img src={logo} alt='logo' />
      </a>
    </div>
  );
};
