import React, { useEffect, useState } from 'react';
import {
  DetailSolution,
  Capacity,
} from '../../../../../../core/interfaces/Solution';
import { DescriptionArtifact } from './description-artifact.component';
import { CardArtifactResponsive } from './card-artifact.component';
import { CardArtifactDesktop } from './card-artifact-desktop.component';
import useCapabilitiesCard from '../../../../../../core/hooks/useCapabilitiesCard';

export const SolutionsArtifactLight: React.FC<{
  detail: DetailSolution;
  tecnology: string;
}> = props => {
  const [state, setState] = useState<{
    rotateCard: boolean;
  }>();
  const captabilities = useCapabilitiesCard(props.detail.capabilities);

  useEffect(() => {
    setState({
      rotateCard: false,
    });
  }, []);

  const flipCardName =
    state?.rotateCard
      ? 'card-container manual-flip flip'
      : 'card-container manual-flip';
  return (
    <React.Fragment>
      <div className='p-md-5 p-4 d-none d-lg-block'>
        <div className='container-lg'>
          <div className='row'>
            <DescriptionArtifact
              key='mobile-description'
              responsive={false}
              description={props.detail.description}
              tecnology={props.tecnology}
              benefits={props.detail.benefits}
            ></DescriptionArtifact>
            <div className='col-xl-7 col-lg-8 col-md-12 col-sm-12 text-center px-lg-5'>
              {captabilities.map((capacity: Capacity[], index: number) => {
                return (
                  <div
                    className='row mb-4'
                    key={`${capacity.length}-${index}-card-artifac-desktop`}
                  >
                    <CardArtifactDesktop
                      capabilities={capacity}
                    ></CardArtifactDesktop>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </div>

      <div className='p-md-5 p-4 d-lg-none'>
        <div className='container-lg'>
          <div className='row justify-content-md-center'>
            <div className='col-12 col-md-6'>
              <div className={flipCardName}>
                <div className='card-content-flip'>
                  <DescriptionArtifact
                    key='desktop-description'
                    responsive={true}
                    description={props.detail.description}
                    tecnology={props.tecnology}
                    benefits={props.detail.benefits}
                    rotateCard={() => setState({ rotateCard: false })}
                  ></DescriptionArtifact>
                  <div className='card back'>
                    <div className='card-body'>
                      <button
                        className='btn-no-style'
                        title=''
                        onClick={() =>
                          setState({ rotateCard: !state?.rotateCard })
                        }
                        data-original-title='Flip Card'
                      >
                        <div className='row'>
                          <CardArtifactResponsive
                            capabilities={props.detail.capabilities}
                          ></CardArtifactResponsive>
                        </div>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};
