import React from 'react';
import logo from '../../../../../assets/img/imagen_arquitectura.png';

export const SectionArchitecture: React.FC<{}> = () => {
  return (
    <section id='arquitectura' className='p-md-5 p-4'>
      <div className='container-lg'>
        <div className='row'>
          <div className='col-lg-5 col-md-12 col-sm-12 mt-lg-5'>
            <h2>Arquitectura</h2>
            <h6>
              Componentes base de arquitectura que resuelvan problemas comunes
              en el desarrollo de software, guidelines de arquitectura y
              estándares de desarrollo con unas garantías de calidad
              excepcionales, acorde a la evolución de las tecnologías.
            </h6>
          </div>
          <div className='col-lg-7 col-md-12 col-sm-12 text-center'>
            <img className='img-fluid' src={logo} alt='' />
          </div>
        </div>
      </div>
    </section>
  );
};
