import React from 'react';
import logo from '../../../../../assets/img/plane.svg';

export const ConatactUs: React.FC<{}> = () => {
  return (
    <section id='contactenos' className='p-md-5 p-4'>
      <div className='container-lg'>
        <div className='row'>
          <div className='col-6'>
            <h2 className='text-body'>Contáctanos</h2>
            <h6>Si necesitas más información, no dudes en escribirnos</h6>
            <h6>
              <a href='mailto:ecnf.latam@everis.com'>ecnf.latam@everis.com</a>
            </h6>
          </div>
          <div className='col-6 text-right'>
            <img
              className='text-center img-fluid'
              src={logo}
              alt='Imagen de front end logo'
            />
          </div>
        </div>
      </div>
    </section>
  );
};
