import { Solution } from '../../core/interfaces/Solution';
import { SolutionsEntity } from './solutions-entity';

export default class SolutionsMapper {
  static mapFrom(solutions: Solution[]) {
    return solutions.map(solution => {
      return new SolutionsEntity(
        solution.id,
        solution.name,
        solution.path,
        solution.logo,
        solution.title,
        solution.detail
      );
    });
  }
}
