import React from 'react';
import { Carrousel } from '../../carrousel/corrousel.component';
import { SolutionsEntity } from '../../../../../../data/repository/solutions-entity';
import { SolutionsArtifactLight } from './solutions-artifact-light.component';
import { SolutionsArtifactBalck } from './solutions-artifact-black.component';

export const SectionSolutions: React.FC<{
  solutions: SolutionsEntity[];
}> = props => {
  return (
    <React.Fragment>
      <section id='soluciones' className='p-md-5 p-4 bg-img-dark'>
        <div className='container'>
          <div className='row'>
            <div className='col-12'>
              <h2 className='text-center text-white'>Soluciones</h2>
            </div>
            <div className='col-12'>
              <Carrousel solutions={props.solutions}></Carrousel>
            </div>
          </div>
        </div>
      </section>
      {props.solutions.map((solution, index) => {
        let isImpar = (index + 1) % 2;
        if (isImpar) {
          return (
            <SolutionsArtifactLight
              key={index}
              tecnology={solution.name}
              detail={solution.detail}
            ></SolutionsArtifactLight>
          );
        }
        return (
          <SolutionsArtifactBalck
            key={index}
            tecnology={solution.name}
            detail={solution.detail}
          ></SolutionsArtifactBalck>
        );
      })}
    </React.Fragment>
  );
};
